<template>
  <transition>
    <div class="wrapper">
      <div class="login-header">
        <i class="icon-fanhui" @click="back"></i>
        <p class="title">登录</p>
        <!-- <div class="btn-loginWay" @click="changeLoginWay">{{isPnonelogin? "密码登录":"短信登录"}} </div> -->
      </div>

      <div class="loginContainer" v-if="!token">

        <!-- 手机验证码登录 -->

        <form class="loginForm" v-if="isPnonelogin">
          <!-- <section class="input_container phone_number">
                <input type="text" placeholder="手机号码" name="phone" maxlength="11" v-model="phoneNumber" />
                <button @click.prevent="getVerifyCode" :class="{right_phone_number:rightPhoneNumber}" >{{!computedTime?'获取验证码': '已发送('+computedTime+'s)'}}</button>

            </section>
            <section class="input_container">
                <input type="text" placeholder="验证码" name="mobileCode" maxlength="6" v-model="mobileCode">
            </section> -->
        </form>
        <!-- 手机账号 密码登陆 -->
        <form class="loginForm" v-else>
          <section class="input_container">
            <input type="text" placeholder="手机号码" v-model.lazy="formPw.phoneNum">
          </section>
          <section class="input_container">
            <input v-if="!showPassword" type="password" placeholder="密码" v-model="formPw.password">
            <input v-else type="text" placeholder="密码" v-model="formPw.password">
            <div class="button_switch" :class="{change_to_text: showPassword}" @click="changePassWordType">
              <div class="circle_button" :class="{trans_to_right: showPassword}"></div>
              <span>abc</span>
            </div>
          </section>
          <section class="input_container captcha_code_container">
            <input type="text" placeholder="验证码" maxlength="4" v-model="formPw.yzm_pw">
            <div class="img_change_img">
              <img v-show="captchaCode.img" :src="captchaCode.img" @click="getCaptchaCode">
              <!-- <div class="change_img" @click="getCaptchaCode">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div> -->
            </div>
          </section>
        </form>
        <!-- <p class="login_tips">
            温馨提示：未注册过的账号，登录时将自动注册
        </p>
        <p class="login_tips">
            注册过的用户可凭账号密码登录
        </p> -->
        <div class="login_container" @click="submit">登录</div>
        <!-- <router-link to="/forget" class="to_forget" v-if="!isPnonelogin">重置密码？</router-link> -->
        <alert-tip v-if="showAlert" :showHide="showAlert" @closeTip="closeTip" :alertText="alertText"></alert-tip>
      </div>
       <div v-else>
        <p class="loginout_tx">已经登陆
            <br>
           {{token}}--{{name}}
          <img :src="avatar">
          <!-- <img src="" alt="" srcset=""> -->
        </p>

        <div class="login_container" @click="loginOutHandle">登出</div>
      </div>
    </div>
  </transition>
</template>

<script type="text/javascript">
import ApiLogin from '@/api/login';
import alertTip from '@/components/alertTip';
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      isPnonelogin: false,
      showPassword: false, // 是否显示密码
      captchaCode: {
        img: null,
        code: null
      },
      formPw: {
        phoneNum: null,
        password: null,
        yzm_pw: null
      },
      formPhone: {
        phoneNum: null,
        yzm_pnone: null
      },
      showAlert: false,
      alertText: ""
    }
  },
  components: {
    alertTip
  },
  computed: {
    ...mapGetters([
      'token','name','avatar'
    ]),
  },
  created() {
    this.getCaptchaCode();
  },
  methods: {
    back() {
      this.$router.back()
    },
    //改变登录方式
    changeLoginWay() {
      this.isPnonelogin = !this.isPnonelogin;
    },
    //是否显示密码
    changePassWordType() {
      this.showPassword = !this.showPassword;
    },
    //获取验证码，线上环境使用固定的图片，生产环境使用真实的验证码
    async getCaptchaCode() {
      let res = await ApiLogin.getcaptchas();
      console.log(res);
      this.captchaCode.img = res.img;
      this.captchaCode.code = res.code;
    },

    async submit() {
      if (this.loginWay) {
        // if (!this.rightPhoneNumber) {
        //     this.showAlert = true;
        //     this.alertText = '手机号码不正确';
        //     return
        // }else if(!(/^\d{6}$/gi.test(this.mobileCode))){
        //     this.showAlert = true;
        //     this.alertText = '短信验证码不正确';
        //     return
        // }

        // this.userInfo = await this.$store.dispatch('LoginWithPhone',{
        //   mobileCode:this.mobileCode,
        //   phoneNumber:this.phoneNumber,
        //   validate_token:this.validate_token
        // })
        //手机号登录
        // this.userInfo = await ApiLogin.sendLogin(this.mobileCode, this.phoneNumber, this.validate_token);
      } else {
        if (!this.formPw.phoneNum) {
          this.showAlert = true;
          this.alertText = '请输入手机号/邮箱/用户名';
          return
        } else if (!this.formPw.password) {
          this.showAlert = true;
          this.alertText = '请输入密码';
          return
        } else if (!this.formPw.yzm_pw) {
          this.showAlert = true;
          this.alertText = '请输入验证码';
          return
        }
        //账号密码登录
        this.userInfo = await this.$store.dispatch("LoginWithPassword", {
          userAccount: this.formPw.phoneNum,
          passWord: this.formPw.password,
          codeNumber: this.formPw.yzm_pw
        });
        console.log(this.userInfo);
      }
      //如果返回的值不正确，则弹出提示框，返回的值正确则返回上一页
      if (!this.userInfo.token) {
        this.showAlert = true;
        this.alertText = this.userInfo.message;
        if (!this.loginWay) this.getCaptchaCode();
      } else {
        // this.RECORD_USERINFO(this.userInfo);
        this.$router.go(-1);

      }
    },
    closeTip() {
      this.showAlert = false;
    },
    rightPhoneNumber: function (pnum) {
      return /^1\d{10}$/gi.test(pnum)
    },
    async loginOutHandle (){
      let res = await this.$store.dispatch('LogOut');
      if(res =="success"){
        //  location.reload();
      }
    },
    // missName() {
    //   this.$refs.missName.style.display = "block"
    //   setTimeout(() => {
    //     this.$refs.missName.style.display = "none"
    //   }, 1000);
    // },
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~styles/variable"
  @import "~styles/mixin"
.wrapper
  position: fixed
  top: 0
  bottom: 0
  z-index: 100
  width: 100%
  background: $color-background
  &.v-enter-active, &.v-leave-active
    transition: all 0.3s
  &.v-enter, &.v-leave-to
    transform: translate3d(-100%, 0, 0)
  .login-header
    position: relative
    top: 0
    left: 0
    width: 100%
    height: 44px
    line-height: 44px
    background: $color-theme
    text-align: center
    .icon-fanhui
      position: absolute
      display: inline-block
      margin-left: 10px
      font-size: $font-size-large-xl
      left: 0
    .title
      font-size: $font-size-large-x
    .btn-loginWay
      position absolute
      height 44px
      line-height 44px
      top 0
      right 4px

.loginContainer{
        // padding-top: 1.95rem;
    }

.loginForm{
    background-color: #fff;
    margin-top: .6rem;
    .input_container{
        display: flex;
        justify-content center;
        align-items center;
        border-bottom: 1px solid #f1f1f1;
        input{
            font-size : $font-size-medium-x
            flex:1;
            padding: 12px 15px;
        }

        .right_phone_number{
            background-color: #4cd964;
            padding: 0
        }
    }
    .phone_number{
      button{
            align-self :stretch
            border: 1px;
            padding: 10px;
            width: 90px;
        }
    }
    .captcha_code_container{
        .img_change_img{
            display: flex;
            align-items: center;
            width:100px;
            img{
                // @include wh(3.5rem, 1.5rem);
                margin-right: 5px;
            }
            .change_img{

                width: 2rem;

                p{
                    // @include sc(.55rem, #666);
                }
                p:nth-of-type(2){
                    color: #3b95e9;
                    margin-top: 5px;
                }
            }
        }
    }
}
.login_tips{
    // @include sc(.5rem, red);
    padding: .4rem .6rem;
    line-height: .5rem;
    a{
        color: #3b95e9;
    }
}
.login_container{
    margin: 50px  10px ;
    font-size :20px;
    background-color: #4cd964;
    padding: 10px 0;
    border: 1px;
    border-radius: 4px;
    text-align: center;
}
.button_switch{
  margin-right: 8px;
    align-self:center;
    position: relative;
    background-color: #ccc;
    // display: flex;
    // justify-content: center;
    width:50px;
    height:30px;
    border: 1px;
    border-radius: 35px;
    .circle_button{
        transition: all .3s;
        position: absolute;
        top: -2px;
        z-index: 1;
        left: 0;
        width: 34px
        height 34px
        box-shadow: 0 4px 4px 0 rgba(0,0,0,.1);
        background-color: #f1f1f1;
        border-radius: 50%;
    }
    .trans_to_right{
        transform: translateX(20px);
    }
    span{
        // @include sc(.45rem, #fff);
        // transform: translateY(.05rem);
        line-height:30px;
    }
    span:nth-of-type(2){
        transform: translateY(-.08rem);
    }
}
.change_to_text{
    background-color: #4cd964;
}
.to_forget{
    float: right;
    // @include sc(.6rem, #3b95e9);
    padding-right: .3rem;
}

.loginout_tx{
  padding:20px 0;
  font-size 20px;
  color #000;
  text-align center;
  img{
    width 100px
    height 100px
  }
}
</style>



