 <template>
  <div class="alert_container">
    <section class="tip_text_container">
      <div class="tip_icon">
        <span></span>
        <span></span>
      </div>
      <p class="tip_text">{{alertText}}</p>
      <div class="confrim" @click="closeTip">确认</div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      positionY: 0,
      timer: null,
    }
  },
  mounted() {

  },
  props: ['alertText'],
  methods: {
    closeTip() {
      this.$emit('closeTip')
    }
  }
}
</script>


<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~styles/variable"
@import "~styles/mixin"
@keyframes tipMove{
    0%   { transform: scale(1) }
    35%  { transform: scale(.8) }
    70%  { transform: scale(1.1) }
    100% { transform: scale(1) }
}
.alert_container{
  position: fixed;
  display flex
  justify-content center
  align-items center
  top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
}
.tip_text_container{
    display: flex;
    width:75vw
    animation: tipMove .4s ;
    background-color: rgba(255,255,255,1);
    border: 1px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    // border: 1px;
    padding-top 15px
    border-radius: 8px;
    .tip_icon{
        // @include wh(3rem, 3rem);
        width 40px
        height 40px
        border: 0.15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        span:nth-of-type(1){
            // @include wh(.12rem, 1.5rem);
            width 3px
            height 18px
            background-color: #f8cb86;
        }
        span:nth-of-type(2){
            // @include wh(.2rem, .2rem);
              width 4px
            height 4px
            border: 1px;
            border-radius: 50%;
            margin-top: .2rem;
            background-color: #f8cb86;
        }
    }
    .tip_text{
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{

        font-weight: bold;
        margin-top: 20px;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 40px;
        border: 1px;
        border-bottom-left-radius: 0.25rem;
        border-bottom-right-radius: 0.25rem;
    }
}

</style>



